// 引入组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import { BTN_TEXT, STATUS_CODE, TITLE } from '@/utils/constantsFile';
import detailTable from '@/views/report/OperationRate_andon/detailTable.vue';
import chartModule from '@/views/report/OperationRate_andon/chartModule.vue';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import SearchBox from '@/components/common/SearchBox';

// 引入通用接口
import { deviceGroupGetTreeFunc } from '@/utils/publicFunction';
import { deviceGroupGetTreeAction, deviceGetPageDeviceComplexByConditionAction } from '@/api/management';
// import { getPageDeviceByCondition } from '../../../../api/management';
import { listAndonMttRMtbF } from '@/api/andon.js';
export default {
  name: 'EquipmentMonitoring',
  data() {
    return {
      // tab默认激活下标
      BTN_TEXT: BTN_TEXT,
      searchData: {
        dateVal: this.$utils.toDateString(new Date(), 'yyyy-MM-dd'),
        statisticsType: 1,
        dateType: 'month',
        standardVal: '90'
      },
      typeOption: [
        {
          name: '车间',
          value: 2
        },
        {
          name: '产线',
          value: 1
        },
        {
          name: '设备',
          value: 0
        }
      ],
      nowOption: [],
      nowOptionx: [],
      searchSelectList: [
        { value: [4] }
      ],
      currentName: '',
      activeN: 0,
      selectTabs: [
        {
          type: 0,
          name: '图表'
        },
        {
          type: 1,
          name: '列表'
        }
      ],
      loading: false,
      allData: []
      // allData: new Array(12).fill(0).map((item, index) => ({
      //   index: index + 1,
      //   value: Math.random() * 100
      // }))
    };
  },
  created() {
    // 调用列表查询接口
    // this.getWarnCount();
    // this.searchData.dateVal = this.$utils.toDateString(new Date(), 'yyyy-MM');
    this.getGroupTree();
    this.search();
  },
  methods: {
    toggleN(i) {
      this.activeN = i;
      this.search();
    },
    getValue3(e) {
      console.log(e);
      this.searchSelectList = [
        { value: null }
      ];
      this.nowOption = [];
      if (e === 0) {
        this.getDev();
      } else {
        this.getGroupTree();
      }
    },
    getValue(e, index) {
      this.searchSelectList[index].value = e[e.length - 1];
      let obj = {};
      obj = this.nowOptionx.find((item) => {
        return item.id === e[e.length - 1];
      });
      this.currentName = obj.name;
    },
    getValue2(e, index) {
      this.searchSelectList[index].value = e + '';
      let obj = {};
      obj = this.nowOption.find((item) => {
        return item.id === e;
      });
      this.currentName = obj.deviceName;
    },
    async search() {
      try {
        this.loading = true;
        this.allData = [];
        const { dateType, dateVal, statisticsType } = this.searchData;
        const res = await listAndonMttRMtbF({}, {
          shiftStart: '',
          shiftEnd: '',
          startDate: dateType === 'month' ? dateVal.substring(0, 8) + '01' : dateVal.substring(0, 5) + '01-01',
          endDate: dateType === 'month' ? dateVal.substring(0, 8) + '31' : dateVal.substring(0, 5) + '12-31',
          statisticsType,
          statisticsValues: this.searchSelectList.map(item => item.value[item.value.length - 1])
        });
        this.allData = res.rows[0] || [];
        this.currentName = this.nowOptionx.find((item) => {
          const ids = this.searchSelectList[0].value;
          return item.id === ids[ids.length - 1];
        })?.name;
        this.loading = false;
      } catch (error) {
        this.loading = false;
      }
    },
    reset() {
      this.searchData.statisticsType = 1;
      this.nowOption = [];
      this.searchSelectList = [
        { value: '' }
      ];
      this.getGroupTree();
    },
    /**
     * @description: 获取类型树数据
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    async getGroupTree() {
      try {
        const res = await deviceGroupGetTreeFunc();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nowOption = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
        const resx = await deviceGroupGetTreeAction();
        if (resx.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nowOptionx = resx.rows;
          this.currentName = this.nowOptionx.find((item) => {
            const ids = this.searchSelectList[0].value;
            return item.id === ids[ids.length - 1];
          })?.name;
        } else {
          // 提示异常信息
          showWarningMessage(resx.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getDev() {
      try {
        const res = await deviceGetPageDeviceComplexByConditionAction({
          current: 1,
          size: 99999999
        },
        {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nowOption = res.rows.map((item) => {
            item.id = item.deviceCode;
            item.name = item.deviceName;
            return item;
          });
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  },
  components: {
    // 加载未审核组件
    SearchBox,
    HeaderTitle,
    HeadOperationItem,
    detailTable,
    chartModule
  }
};
