export default {
  props: {
    chartData: {
      type: Array,
      default: () => []
    },
    param: {
      type: Object
    }
  },
  data() {
    return {};
  },
  computed: {

  },
  watch: {
  },
  created() {},
  mounted() {
    const { dateType, standardVal } = this.param;
    const unit = dateType === 'month' ? '日' : '月';
    const seriesData = [];
    const xData = [];
    const standardData = [];
    this.chartData.forEach(item => {
      xData.push(item.index + unit);
      seriesData.push(item.utilization.toFixed(2));
      standardData.push(standardVal);
    });
    this.draw(xData, seriesData, standardData);
  },
  methods: {
    draw(xData = [], seriesData = [], standardData) {
      this.myChart = this.$echarts.init(this.$refs.echartGantt);
      this.myChart.setOption({
        tooltip: {
          trigger: 'axis',
          // axisPointer: {
          //   type: 'cross',
          //   crossStyle: {
          //     color: '#999'
          //   }
          // },
          formatter: (params) => {
            let str = params[0].name;
            params.forEach((item, index) => {
              str += `<br>${item.marker}${item.seriesName}: ${item.value}%`;
            });
            return str;
          }
        },
        toolbox: {
          show: true,
          feature: {
            saveAsImage: {
              show: true
            }
          }
        },
        legend: {
          right: 40
        },
        grid: {
          containLabel: true,
          left: '3%',
          right: '5%'
        },
        dataZoom: [
          {
            type: 'slider',
            filterMode: 'weakFilter',
            top: 'bottom'
          }
          // {
          //   type: 'inside',
          //   filterMode: 'weakFilter'
          // }
        ],
        xAxis: [
          {
            type: 'category',
            data: xData,
            axisPointer: {
              type: 'shadow'
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '%',
            min: 0
            // max: 250,
            // interval: 50,
            // axisLabel: {
            //   formatter: (value, index) => {
            //     return parseInt(value / 60 / 60);
            //   }
            // }
          }],
        series: [
          {
            name: '成型机',
            type: 'line',
            yAxisIndex: 0,
            // smooth: true,
            symbolSize: 5,
            itemStyle: {
              normal: {
                color: '#326FD5'
              }
            },
            label: {
              show: true,
              position: 'top'
            },
            data: seriesData
          },
          {
            name: '稼动率标准线',
            type: 'line',
            itemStyle: {
              normal: {
                color: '#8ECC4B'
              }
            },
            smooth: false,
            symbolSize: 0.01,
            data: standardData
          }
        ]
      });
    },
    timeStampSeconds(cellValue) {
      var day = parseInt(cellValue / 60 / 60 / 24);
      var hour = parseInt(cellValue / 60 / 60 % 24);
      var min = parseInt(cellValue / 60 % 60);
      var s = parseInt(cellValue % 60);
      cellValue = '';
      if (day > 0) {
        cellValue = day + '天';
      }
      if (hour > 0) {
        cellValue += hour + '小时';
      }
      if (min > 0) {
        cellValue += parseFloat(min) + '分钟';
      }
      if (s > 0) {
        cellValue += parseFloat(s) + '秒';
      }
      return cellValue;
    }
  }
};
